import React, { useEffect } from "react";
import { Grid, IconButton, Avatar } from "@material-ui/core";

const TechnologiesComponent = () => {

  const getTech = () => {
    fetch('technologies.json'
    ,{
      headers : { 
        'Content-Type': 'application/json',
        'Accept': 'application/json'
       }
    }
    )
      .then(function(response){
        console.log(response)
        return response.json();
      })
      .then(function(myJson) {
        console.log(myJson);
      });
  }

  useEffect(()=>{
    getTech();
  },[])

  return (
    <Grid item container xs={5} justify='center' spacing={1} style={{border:'1px solid white',borderRadius:50,padding:'0 0 0 0'}}>
      <Grid item>
        <IconButton size='small'>
          <Avatar src="https://img.icons8.com/color/48/000000/docker.png"/>
        </IconButton>
      </Grid>
      <Grid item>
        <IconButton size='small'>
          <Avatar src="https://img.icons8.com/fluent/48/000000/laravel.png"/>
        </IconButton>
      </Grid>
      <Grid item>
        <IconButton size='small'>
          <Avatar src="https://img.icons8.com/plasticine/100/000000/react.png"/>
        </IconButton>
      </Grid>
      <Grid item>
        <IconButton size='small'>
          <Avatar src="https://img.icons8.com/color/48/000000/redis.png"/>
        </IconButton>
      </Grid>
      <Grid item>
        <IconButton size='small'>
          <Avatar src="https://img.icons8.com/fluent/48/000000/mysql-logo.png"/>
        </IconButton>
      </Grid>
      <Grid item>
        <IconButton size='small'>
          <Avatar src="https://img.icons8.com/color/48/000000/maria-db.png"/>
        </IconButton>
      </Grid>
      <Grid item>
        <IconButton size='small'>
          <Avatar src="https://img.icons8.com/color/48/000000/nodejs.png"/>
        </IconButton>
      </Grid>
    </Grid>
  );
};

export default TechnologiesComponent;

